.page {
  max-width: 50rem;
  margin-left: auto;
  margin-right: auto;
  font-family: 'Trebuchet MS', sans-serif;
}

.title {
  margin-bottom: 0px;
}

.subtitle {
  margin: 10px 0px;
}

.title-subtext {
  color: rgb(100, 100, 100);
  margin: 10px 0px;
}

.code-snippet {
  display: block;
  border: 1px rgb(120, 120, 120) solid;
  border-radius: 3px;
  padding: 10px;
  margin: 8px 0px;
  white-space: pre-wrap;
}

.example-container {
  display: flex;
  flex-direction: column;
  width: 30rem;
  margin: auto;
  padding: 12px;
}

table.GeneratedTable {
  width: 100%;
  background-color: #ffffff;
  border-collapse: collapse;
  border-width: 1px;
  border-color: rgb(120, 120, 120);
  border-style: solid;
  color: #000000;
  margin-bottom: 30px;
}

table.GeneratedTable td, table.GeneratedTable th {
  border-width: 1px;
  border-color: rgb(120, 120, 120);
  border-style: solid;
  padding: 5px;
}

table.GeneratedTable thead {
  background-color: #e6e6e6;
}
